import React, { useState } from 'react';

const FAQ = () => {
  const [openIndex, setOpenIndex] = useState(null);

  const faqs = [
    {
      question: "Qu documentos necesito para realizar un trmite notarial?",
      answer: "Los documentos requeridos varan segn el tipo de trmite. En general, necesitar su cdula de identidad y certificado de votacin originales. La calculadora muestra los requisitos especficos para cada trmite."
    },
    {
      question: "Cunto tiempo toma realizar un trmite notarial?",
      answer: "El tiempo vara segn el tipo de trmite y la complejidad del caso. Algunos trmites como reconocimientos de firma pueden realizarse el mismo da, mientras otros como escrituras pueden tomar varios das."
    },
    {
      question: "Existen descuentos o exenciones en los trmites notariales?",
      answer: "S, pueden aplicar descuentos especiales segn el tipo de trmite y las caractersticas del usuario. Consulte en la notara si su caso califica para algn descuento o exencin."
    },
    {
      question: "Puedo realizar trmites notariales en lnea?",
      answer: "Algunos trmites pueden iniciarse en lnea, pero la mayora requieren su presencia fsica en la notara para la firma de documentos y verificacin de identidad."
    },
    {
      question: "Qu formas de pago aceptan?",
      answer: "Aceptamos efectivo, tarjetas de dbito y crdito, y transferencias bancarias. El pago debe realizarse antes de la entrega de los documentos."
    },
    {
      question: "Necesito un abogado para realizar trmites notariales?",
      answer: "Para algunos trmites como escrituras, promesas de compraventa y poderes, se requiere una minuta firmada por un abogado. Para otros trmites ms simples como reconocimientos de firma, no es necesario."
    },
    {
      question: "Qu pasa si no tengo todos los documentos requeridos?",
      answer: "No se puede proceder con el trmite hasta tener toda la documentacin requerida. Le recomendamos revisar la lista de requisitos en nuestra calculadora antes de visitar la notara."
    }
  ];

  const toggleFAQ = (index) => {
    setOpenIndex(openIndex === index ? null : index);
  };

  return (
    <div className="container mx-auto px-4">
      <h2 className="text-3xl font-bold text-center mb-12">
        Preguntas Frecuentes
      </h2>
      <div className="max-w-3xl mx-auto space-y-4">
        {faqs.map((faq, index) => (
          <div 
            key={index} 
            className="bg-white rounded-lg shadow-sm overflow-hidden"
          >
            <button
              className="w-full text-left p-6 focus:outline-none hover:bg-gray-50 transition-colors duration-200"
              onClick={() => toggleFAQ(index)}
            >
              <div className="flex justify-between items-center">
                <h3 className="text-xl font-semibold text-gray-900 pr-8">
                  {faq.question}
                </h3>
                <span className={`transform transition-transform duration-200 ${openIndex === index ? 'rotate-180' : ''}`}>
                  
                </span>
              </div>
            </button>
            <div 
              className={`transition-all duration-300 ease-in-out ${
                openIndex === index 
                  ? 'max-h-96 opacity-100' 
                  : 'max-h-0 opacity-0'
              } overflow-hidden`}
            >
              <p className="text-lg text-gray-600 p-6 pt-0 whitespace-pre-line">
                {faq.answer}
              </p>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default FAQ;
